        headers: {
          Authorization:
            "Basic MTAyZTUwNzRmMTNmYmFjMzUwYzYwZDNjMTMwODIwNjA5NTE0MzgzN2E4NzEzOWExOjZlODBjNTIzNTdiZTJjNTcyZDdiY2I0ODg0M2FlZWY1ODhkMjZhYTViZTczNjQzYw==",
          accept: "application/json",
          "Content-Type": "application/x-www-form-urlencoded",
        },
        data: data,
      };
      console.log("config", config);
      axios
        .request(config)
        .then((response) => {
          console.log(JSON.stringify(response.data));
        })
        .catch((error) => {
