import { createRouter, createWebHistory } from "vue-router";
import LoginPage from "../views/LoginPages/LoginPage.vue";
import CesiumTest from "../views/LoginPages/CesiumTest.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: LoginPage,
  },
  {
    path: "/home-page",
    name: "home-page",
    component: CesiumTest,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
